import React, { Component } from "react";
// import { Card, Button, Accordion } from "react-bootstrap";
// import { Card } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Collapse from "@material-ui/core/Collapse";
import clsx from 'clsx';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { makeStyles } from '@material-ui/core/styles';

// import {Card, CardActions, CardHeader, CardText, CardContent} from 'material-ui/core/Card';
import "./EventCard.css";
import { CardActions } from "@material-ui/core";
class EventCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      expand: false
    };
  }

  handleExpandClick = () => {
    this.setState({ expand: !this.state.expand });
  };

  render() {
    
    const { item, eventTypeList } = this.props;
    // let expanded = this.state.expanded;
    var months = [
      "JAN",
      "FEB",
      "MAR",
      "APR",
      "MAY",
      "JUN",
      "JUL",
      "AUG",
      "SEP",
      "OCT",
      "NOV",
      "DEC"
    ];

    var colors = [
      "#F08080",
      "#ADD8E6",
      "#FFFACD",
      "#87CEFA",
      "#20B2AA",
      "#FFA07A",
      "#00FA9A",
      "#FFE4E1",
      "#DA70D6",
      "#FFF5EE",
      "#87CEEB",
      "#F5DEB3",
      "#F0F8FF",
      "#FF7F50"
    ];
    let startDate = new Date(item.date);
    var startHr = startDate.getHours();
    var startMin = startDate.getMinutes();
    if (startMin < 10) {
      startMin = "0" + startMin;
    }
    var startampm = "AM";
    if (startHr > 12) {
      startHr -= 12;
      startampm = "PM";
    }

    let endDate = new Date(item.date);
    var endHr = endDate.getHours();
    var endMin = endDate.getMinutes();
    if (endMin < 10) {
      endMin = "0" + endMin;
    }
    var endampm = "AM";
    if (endHr > 12) {
      endHr -= 12;
      endampm = "PM";
    }

    let month = months[startDate.getMonth()];
    let date = startDate.getDate();
    let indexColor = eventTypeList && eventTypeList.indexOf(item.type);
    return (
      <div>
        <Card
          style={{ backgroundColor: colors[indexColor] }}
          className="event-card"
        >
          <CardHeader
          title={item.eventName}
          subheader={item.type}
          />
            {/* <CardContent as="h6">{item.eventName}</CardContent>
            <h3 className="event-type">{item.type}</h3> */}
            {/* <Button onClick={handleExpandClick}></Button> */}
            {/* <p className="event-time">
              {startHr + ":" + startMin + " " + startampm + " "}-
              {" " + endHr + ":" + endMin + " " + endampm}
            </p>
            <p>
              {item.ageGroup} 
            </p> */}
          <CardActions disableSpacing>
            <IconButton onClick={this.handleExpandClick}>
              <ExpandMoreIcon />
            </IconButton>
            {/* <IconButton
              className={clsx(classes.expand, {
                [classes.expandOpen]: expanded,
              })}
              onClick={handleExpandClick}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMoreIcon />
            </IconButton> */}
          </CardActions>
          <Collapse in={this.state.expand} timeout="auto" unmountOnExit>
            <CardContent>
              <Typography paragraph>{item.descript}</Typography>
              <Typography paragraph>Address: {item.address}</Typography>
              <Typography paragraph>Insta: {item.insta}</Typography>
              <Typography paragraph>{item.website}</Typography>
            </CardContent>
          </Collapse>
        </Card>
        <Card className="event-card date-card">
          <CardContent>
            <h3 className="event-type month">{month}</h3>
            <h3 className="event-type date">{date}</h3>
          </CardContent>
          {/* <CardHeader 
            title={month}
            subheader={date}
          /> */}
            {/* <h3 className="event-type month">{month}</h3>
            <h3 className="event-type date">{date}</h3> */}
          
        </Card>
      </div>
    );
  }
}

export default EventCard;
